.blog{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.topic-grid{
    display: flex;
    width: 70%;
    height: 70vh;
    margin: 9vh auto;
    flex-wrap: wrap;
    border-radius: 6px;
    border: 2px solid #010101;
}

.split-left{
    flex: 4 1;
    display: flex;
    flex-direction: column;
}

.link-box{
    text-decoration: none;
    display: flex;
    flex-direction: column;
    -webkit-transition: all .2s;
    transition: all .2s;
}

.link-box:hover{
    border: 2px solid #010101;
    cursor: pointer;
}

.topic1{
    flex: 1 1;
    background-image: url(/coffee-blog/static/media/coffee.4df9e6f7.jpeg);
    background-size: 100% 100%;
}

.topic2{
    flex: 1 1;
    background-image: url(/coffee-blog/static/media/tech.b60efb6a.jpeg);
    background-size: 100% 100%;
}

.topic3{
    flex: 3 1;
    background-image: url(/coffee-blog/static/media/outdoors.f57a34c0.jpeg);
    background-size: 100% 100%;
}
.topics-tile{
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
    object-fit: cover;
    display: flex;
    justify-content: center;
}

.topics-tile:hover{
    text-decoration: none;
}

.topics-tile-title{
   color: black;
   text-decoration: none;
   margin: 10px;
   padding-top: 10px;
   padding-bottom: 10px;
   font-size: 1.5em;
}
.blogHome {
    padding: 10px;
}

.blogHeader{
    display: block;
    text-align: center;
}

.tile-group{
    display: flex;
    justify-content: center;
}

.link-area{
    text-decoration: none;
    margin: 1rem;
}

.article-tile{
    flex-wrap: wrap;
    border: 4px solid;
    border-radius: 4px;
    border-color: black;
    padding: 20px;
    z-index: 1;
}

.article-tile:hover{
    border: 2px solid #010101;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.25), 0 10px 10px 0 rgba(0, 0, 0, 0.25);
    cursor: pointer;
}

.article-tile-title{
    color: black;
    font: inherit;
    font-size: 2rem;
}

.article-group{
    display: flex;
    flex-direction: column;
    width: 70%;
    border-radius: 4px;
    padding: 20px;
    margin: 2% auto;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.25), 0 10px 10px 0 rgba(0, 0, 0, 0.25);
    z-index: 1;
}
